import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhSupplierReturnInvoiceDetail, VhSupplierReturnInvoices } from 'src/app/sales/interface/vh-supplier-return-invoice';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-detail-bill-supplier-return',
  templateUrl: './detail-bill-supplier-return.component.html',
  styleUrls: ['./detail-bill-supplier-return.component.scss']
})
export class DetailBillSupplierReturnComponent {

  public invoiceInfo: any;
  public invoice: VhSupplierReturnInvoices
  constructor(
    private router: Router,
    private vhQuerySales: VhQuerySales,
    private languageService: LanguageService,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm
  ) {
    let state: any = this.router.getCurrentNavigation().extras.state
    if (state) {
      this.data = state.data;
      this.stateObj = state.stateObj;
      this.bills = state.bills;
    }
    this.getDataInvoice(state._id)
    this.init();
  }
  stateObj: any;
  bills: any;
  data: any;
  totalQuantity: number = 0;
  init(invoice: VhSupplierReturnInvoices = null, invoiceDetail: VhSupplierReturnInvoiceDetail[] = null) {
    this.invoice = new VhSupplierReturnInvoices(invoice, invoiceDetail)
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.getIdEmployee()),
        partner: this.vhQuerySales.getlocalSupplier(this.invoice.getIdPartner()),
        paymentName: this.setPaymentName(this.invoice.getPaymentType())
      }
    } else this.invoiceInfo = { employee: {}, partner: {}, paymentName: '' }
  }

  private getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQuerySales.getBill(id),
        this.vhQuerySales.getBill_details_byId_bill(id)
      ]).then(([invoice, invoiceDetail]: any) => {
        let invoiceDetailFresh: VhSupplierReturnInvoiceDetail[] = invoiceDetail.map(item => {
          let product = this.vhQuerySales.getlocalProduct(item.id_product)
          return { ...item, name: product.name ? product.name : this.languageService.translate('Not found'), unit: product.unit }
        })
        this.init(invoice, invoiceDetailFresh)

        this.vhComponent.hideLoading(0)
      })
    })

  }

  public setPaymentName(type: 1 | 2 | 3) {
    switch (type) {
      case 1: return this.languageService.translate("Cash")
      case 2: return this.languageService.translate("Debit")
      case 3: return this.vhQuerySales.getlocalWallet(this.invoice.getIdWallet()).name
    }
  }
  public goToCart() {
    this.router.navigate(['mobile/sales/cart'], { state: { invoiceDetail: this.invoice.getInvoiceDetail(), invoice: this.invoice.getCreateUpdateInvoice() } })
  }
  public renderAddress(partner) {
    return (partner.address ? partner.address : '') +
      (partner.district ? ', ' + partner.district : '') +
      (partner.province ? ', ' + partner.province : '') +
      (partner.country ? ', ' + partner.country : '')
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/cashflow/supplier-liability/detail-supplier-liability'], {
      state: {
        data: this.data,
        stateObj: this.stateObj,
        bills: this.bills
      }
    });
  }

}
