import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-detail-receipt-expenses',
  templateUrl: './detail-receipt-expenses.component.html',
  styleUrls: ['./detail-receipt-expenses.component.scss']
})
export class DetailReceiptExpensesComponent implements OnInit {

  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    public languageService: LanguageService,
    private vhQuerySales: VhQuerySales
  ) {
    this.billInfo = { ...this.router.getCurrentNavigation().extras.state };
    this.data = this.billInfo.data;
    this.stateObj = this.billInfo.stateObj;
    this.bills = this.billInfo.bills;
    this.billInfo['status_bill'] = this.checkBillType(this.billInfo.bill_type);
    if (this.billInfo['id_wallet'] != "id_cash") {
      this.billInfo['payment_type'] = 3;
    }
    else { this.billInfo['payment_type'] = 1 }
  }

  ngOnInit(): void {
    this.list_suppliers = this.vhQuerySales.getlocalSuppliers();
  }
  stateObj: any;
  bills: any;
  billInfo: any;
  data: any;
  list_suppliers: any = [];

  checkBillType(bill_type) {
    //kim tra loi ha n
    switch (bill_type) {
      case 12: {
        return (this.languageService.translate("Pay for supplier liabilities"));
      }
    }
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/cashflow/supplier-liability/detail-supplier-liability'], {
      state: {
        data: this.data,
        stateObj: this.stateObj,
        bills: this.bills
      }
    });
  }
}
